import { useRef, useState } from 'react';
import photo from './photo.jpg';

const User = () => {
   
   const notesRef = useRef();
   const [addedNotes, setAddedNotes] = useState('')

    const name = 'Hannah';
    const image = photo;

    function onaddNotesHandeler(event) {
        event.preventDefault();

      const  enteredNotes= notesRef.current.value;

      fetch('https://shoppinglist-18749-default-rtdb.firebaseio.com/notes.json',
      
        {
            method: 'POST',
            body: JSON.stringify(enteredNotes),
            headers:{
                'Conetent-Type' : 'application/json'
            }
        }
      ).then(()=>{
         
          fetch('https://shoppinglist-18749-default-rtdb.firebaseio.com/notes.json'
          ).then(response =>{
              return response.json();
          }).then(data=> {
            setAddedNotes(data)
          }  )
         
      })
      

    }

    return(
        <div >
         <form className='profileForm' onSubmit={onaddNotesHandeler}>
         <h5>{name}</h5>
         <img src={image} alt='' width='180px' height='250px'/>
         <p>Notes</p>
         <textarea rows='15' cols='70' ref={notesRef} >{addedNotes}</textarea>
         <button className='profileButton' type='submit'>Save</button>
         </form>
        </div>
    )
}

export default User;