import { useState } from "react";

const Type=({onClickHandeler}) =>{

   


    return(
        <div className="settings type">
            <div className="dropdown"  > 
               <p onClick={onClickHandeler} id="Grocery">Grocery</p>
            </div>

           <div className="dropdown"   >
               <p onClick={onClickHandeler} id="Clothing">Clothing</p>
            </div>

            <div className="dropdown"   >
                <p onClick={onClickHandeler} id="Christmas">Christmas</p>
            </div>

            <div className="dropdown"   >
                <p  id="+">+</p>
            </div>
         
        </div>
    )
}
export default Type;