import { useState } from "react";

const Settings=() =>{

   
    const[toggle, setToggle] = useState(false);
    const[toggleS, setToggleS]= useState(false);
    const[toggleT, setToggleT]= useState(false);

    function onClickHandeler(){
        setToggle(!toggle);
    }
    function onClickHandelerSecond(){
        setToggleS(!toggleS);
    }
    function onClickHandelerThird(){
        setToggleT(!toggleT);
    }



    return(
        <div className="settings">
            <div className="dropdown" onClick={onClickHandeler}><p>Language</p>
            {toggle ? 
            <ul  className="dropdown_content">
                <li><p>English</p></li>
                <li><p>French</p></li>
                <li><p>Spanish</p></li>
                <li><p>German</p></li>
            </ul>
            : null}
            </div>
           <div className="dropdown" onClick={onClickHandelerSecond} ><p>Currency</p>
           {toggleS ?
            <ul className="dropdown_content">
                <li><p>US dollar</p></li>
                <li><p>euro</p></li>
                <li><p>CA dollar</p></li>
                <li><p>pound</p></li>
            </ul>
            : null}
            </div>

            <div className="dropdown" onClick={onClickHandelerThird}><p>Color Scheme</p>
            {toggleT ?
            <ul className="dropdown_content">
                <li><p>Green</p></li>
                <li><p>Blue</p></li>
                <li><p>Red</p></li>
                <li><p>BnW</p></li>
                </ul>
            : null}
            </div>
         
        </div>
    )
}
export default Settings;