import {Routes, Route, useNavigate} from 'react-router-dom';
import { useState } from 'react';
import Main from './components/main/Main';
import User from './components/main/User';
import Start from './components/Start';
import SignIn from './components/main/SignIn';
import Register from './components/main/Register';
import Settings from './components/main/nav/Settings';
import Purchases from './components/main/nav/Purchases';
import Type from './components/main/nav/Type';


function App() {
  
  const navigate = useNavigate();
  const [grocery, setGrocery] =useState('');

  function onClickHandeler(event){
     setGrocery(event.target.id)
     navigate('/shoppinglist/main')
  }
  

  return (
    <div className="App">
     <Routes>
      <Route path='/shoppinglist/main' element={<Main  grocery={grocery} />}/>
      <Route path='/shoppinglist/user' element={<User/>}/>
      <Route exact  path='/shoppinglist' element={<Start/>}/>
      <Route path='/shoppinglist/signin' element={<SignIn/>} />
      <Route path='/shoppinglist/register' element={<Register/>} />
      <Route path='/shoppinglist/settings' element={<Settings/>} />
      <Route path='/shoppinglist/purchases' element={<Purchases/>} />
      <Route path='/shoppinglist/type' element= {<Type onClickHandeler={onClickHandeler}  />} />
     </Routes>
     
    </div>
  );
}

export default App;
